import React, { useEffect, useState } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import Buttons from '../ActionButtons/Buttons';
import Modal from '../../components/Modal/Modal';
import ExplainationBoard from '../ExplainationBoard/ExplainationBoard';
import Board from '../NumberBoard/Board';
import classNames from './Layout.module.css';

const finalLayoutItems = [
    {
        id: 'board',
        component: <Board />,
        classNamed: classNames.board
    },
    {
        id: 'explaination',
        component: <ExplainationBoard />,
        classNamed: classNames.explaination
    }
]

const Layout = () => {

    const [layoutItems, setLayoutItems] = useState(finalLayoutItems);
    const [infoModal, setInfoModal] = useState(true);

    useEffect(()=>{
        if(localStorage.getItem("infoModal")){
            let value = localStorage.getItem("infoModal");
            setInfoModal(JSON.parse(value))
        }
    },[])

    function handleOnDragEnd(result) {
        if (!result.destination) return;
    
        const items = Array.from(layoutItems);
        const [reorderedItem] = items.splice(result.source.index, 1);
        items.splice(result.destination.index, 0, reorderedItem);
    
        setLayoutItems(items);
    }

    const closeInfoModal = () => {
        localStorage.setItem("infoModal", false);
        setInfoModal(false)
    }

    return (
        <>
            <Modal show={infoModal} close={closeInfoModal}>
                <div className={classNames.infoModal}>
                    <h4>You can drag the explaination board or number grid to rearrange them. 🤙🏻</h4>
                </div>
            </Modal>
            <div className={classNames.container}>
                <div className={classNames.buttons}>
                    <Buttons />
                </div>
                <DragDropContext onDragEnd={handleOnDragEnd}>
                    <Droppable droppableId="app_ui">
                        {(provided)=>(
                            <div
                            className={classNames.resultSection}
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            >
                                {layoutItems.map(({id, component, classNamed},index) =>{
                                    return (
                                        <Draggable key={id} draggableId={id} index={index}>
                                            {(provided)=>(
                                                <div
                                                className={classNamed}
                                                ref={provided.innerRef}
                                                {...provided.draggableProps}
                                                {...provided.dragHandleProps}
                                                >
                                                    {component}
                                                </div>
                                            )}
                                        </Draggable>
                                    )
                                })}
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
            </div>
        </>
    );
};
export default Layout;